<template>
  <div>
    <v-alert border="right" color="blue-grey" dark v-if="items.lenth > 0">
      {{ items[0] }}
    </v-alert>
    <qrcode-scanner
      :qrbox="250"
      :fps="10"
      style="width: 450px"
      @result="onScan"
    />
    <v-btn block v-on:click="copy"> result (copy) </v-btn>
    <ul>
      <li v-for="item in items" v-bind:key="item">
        {{ item }}
      </li>
    </ul>
    <v-snackbar v-model="snackbar" :timeout="timeout">
      {{ msg }}

      <template v-slot:action="{ attrs }">
        <v-btn color="blue" text v-bind="attrs" @click="snackbar = false">
          Close
        </v-btn>
      </template>
    </v-snackbar>
    <v-alert border="bottom" color="pink darken-1" dark>
      새로고침이 되면 데이터가 사라집니다. 주의하세요!
    </v-alert>
  </div>
</template>

<script>
import QrcodeScanner from "./QrcodeScanner.vue";
export default {
  name: "Scanner",
  components: {
    QrcodeScanner,
  },
  data: function () {
    return {
      items: [],
      snackbar: false,
      msg: "My timeout is set to 2000.",
      timeout: 2000,
    };
  },
  methods: {
    onScan(decodedText, decodedResult) {
      // handle the message here :)
      console.log(decodedText);
      console.log(typeof decodedText);
      console.log(decodedResult);
      console.log(typeof decodedResult);
      if (decodedText != this.items[0]) {
        this.items.unshift(decodedText);
      }
    },
    copy() {
      var text = this.items.join("\n");
      navigator.clipboard
        .writeText(text)
        .then(() => {
          this.timeout = "2000";
          this.msg = "복사에 성공하였습니다";
          this.snackbar = true;
        })
        .catch((err) => {
          this.timeout = "2000";
          this.msg = err;
          this.snackbar = true;
        });
    },
  },
};
</script>
